<template>
    <div>
        <u-button type="primary" @on-click="modal1 = true">Display dialog box</u-button>
        <u-modal
                v-model="modal1"
                :closable="true"
                @on-ok="ok"
                :draggable="true"
                title="asdfasdfasdfasdfasdf"
                drag-rect="30px 0px 100px 60px"
                @on-cancel="cancel">
            <p>Content of dialog</p>
            <p>Content of dialog</p>
            <p>Content of dialog</p>
            <u-button @on-click="openMessage">Message</u-button>
            <u-button type="primary" @on-click="handleSpinShow">整页显示，3秒后关闭</u-button>
        </u-modal>
    </div>
</template>
<script>
    export default {
        data () {
            return {
                modal1: false,
            };
        },
        methods: {
            ok () {
//                this.$Message.info('Clicked ok');
            },
            cancel () {
//                this.$Message.info('Clicked cancel');
            },
            openMessage () {
                this.$Message.info({
                    content: 'hello world',
                    duration: 2
                });
            },
            handleSpinShow () {
                this.$Spin.show();
            },
        }
    };
</script>



